'use strict';

var _requestFunction = require('request-function');

var _toRequestOptions = require('./to-request-options');

var _responseHandler = require('./response-handler');

var send = function send(requestModel) {
  return new Promise(function (resolve, reject) {
    try {
      var options = _toRequestOptions(requestModel);

      var request = _requestFunction.fromProtocol(options.protocol);

      var clientRequest = request(options, function (resp) {
        return _responseHandler(resp, resolve, reject);
      });
      clientRequest.on('timeout', function () {
        clientRequest.destroy();
      });

      if (requestModel.socketTimeout) {
        clientRequest.on('socket', function (socket) {
          socket.setTimeout(requestModel.socketTimeout);
          socket.on('timeout', function () {
            clientRequest.destroy();
          });
        });
      }

      clientRequest.on('error', function (e) {
        reject(e);
      });

      if (requestModel.body) {
        clientRequest.write(requestModel.body);
      }

      clientRequest.end();
    } catch (e) {
      reject(e);
    }
  });
};

module.exports = send;