'use strict';

var http = require('http');

var https = require('https');

var url = require('url');

var typedError = require('error/typed');

var unknownProtocolErrorType = 'request-function/unknown-protocol';
var unknownProtocolError = typedError({
  type: unknownProtocolErrorType,
  message: "Unknown protocol '{protocol}'"
});

var fromProtocol = function fromProtocol(protocol) {
  switch (protocol) {
    case 'http:':
      return http.request;

    case 'https:':
      return https.request;

    default:
      throw unknownProtocolError({
        protocol: protocol
      });
  }
};

var fromUrl = function fromUrl(urlString) {
  var parsedUrl = url.parse(urlString);
  return fromProtocol(parsedUrl.protocol);
};

module.exports = {
  fromProtocol: fromProtocol,
  fromUrl: fromUrl,
  unknownProtocolErrorType: unknownProtocolErrorType
};